body {
  background-image: url(/react-portfolio/static/media/background.da961f7b.jpg);
  background-size: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
  width: 100%;
  height: 80px;
  display: block;
  background-color: grey;
  border-radius: 30px;
  opacity: 0.95;

}
.inner{
  width: 75%;
  height: 100%;
  display: block;
  margin: 0 auto;
}
.logo{
  height: 100%;
  display: table;
  float: left;
}
.logo h1{
  color: white;
  height: 100%;
  display: table-cell;
  vertical-align: middle;
  font-size: 28px;
  font-weight: 100;
}
.logo h1 span{
  font-weight: bold;
}
.navigation{
  float: right;
  height: 100%;
}
.navigation a{
  height: 100%;
  display: table;
  float: left;
  padding: 0px 20px;
  text-decoration: none;
}
.navigation a li{
  display: table-cell;
  vertical-align: middle;
  height: 100%;
  color: white;
  font-size: 18px;
  font-weight:250;
  padding-bottom: 30px;
}
.content{
  margin: 0 auto;
  background-color: white;
  width: 1000px;
  height: 368px;
  margin-top: 10%;
  display: table;
  opacity: 0.95;
}
.message{
  text-align: center;
  padding-top: 10%;
  width: 175px;
  height: 100%;
  background-color: lightgray;
  display: table-cell;
  font-family: cursive;
}
.welcome-image{
  background-image: url(/react-portfolio/static/media/welcome.b908ca87.jpg);
  width: 306px;
  display: table-cell;
  height: 368px;
}
.cv-content{
  width: 50%;
  height: 100%;
  margin: 0 auto;
  margin-top: 20px;
  background: rgba(67, 69, 67, 0.9);
  text-align: center;
  font-family: cursive;
  color: #fff;
  font-size: 90%;
  border-radius: 25px;
  border: 1px solid;
}
